import { lazy } from 'react'

const Users = [
  {
    path: '/users/list',
    exact: true,
    component: lazy(() => import('../../views/Users/List'))
  },
  {
    path: '/users/edit/:id',
    component: lazy(() => import('../../views/Users/Edit'))
  },
  {
    path: '/users/view/:id',
    component: lazy(() => import('../../views/Users/View'))
  },
  {
    path: '/userReport',
    component: lazy(() => import('../../views/Users/Report'))
  }
]

export default Users