/*
 *
 * RecoveryRequest actions
 *
 */

import axios from 'axios'
import { success } from "react-notification-system-redux"

import {
  RESET_RECOVERY_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET_RECOVERY,
  ADD_RECOVERY,
  DELETE_RECOVERY,
  SET_RECOVERIES_LOADING,
  SET_RECOVERIES_SUBMITTING,
  SET_RECOVERIES_FORM_ERRORS,
  RECOVERY_FORM_CHANGE,
  RECOVERY_EDIT_CHANGE,
  DEACTIVATE_RECOVERY
} from './constants'
import handleError from '@utils/error'
import { allFieldsValidation } from '@utils/validation'

export const recoveryFormChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: RECOVERY_FORM_CHANGE,
    payload: formData
  }
}

export const recoveryEditChange = (name, value, type) => {
  const formData = {}
  if (type === 'option') {
    formData[name] = value.value
  } else {
    formData[name] = value
  }

  return {
    type: RECOVERY_EDIT_CHANGE,
    payload: formData
  }
}

// ** Get all Data
export const getAllRecoveries = () => {
  return async dispatch => {
    await axios.get('/api/recoveries').then(response => {
      dispatch({
        type: GET_ALL_DATA,
        data: response.data.recoveries
      })
    })
  }
}

// ** Get data on page or row change
export const getRecoveryList = params => {
  return async dispatch => {
    await axios.get('/api/recoveries', { params }).then(response => {
      dispatch({
        type: GET_DATA,
        data: response.data.recoveries,
        totalPages: response.data.total,
        params
      })
    })
  }
}

// ** Get Recovery
export const getRecovery = id => {
  return async dispatch => {
    await axios
      .get(`/api/recoveries/${id}`)
      .then(response => {
        dispatch({
          type: GET_RECOVERY,
          selectedRecovery: response.data.recovery
        })
      })
      .catch(err => console.log(err))
  }
}

// ** Add recovery
export const addRecovery = () => {
  return async (dispatch, getState) => {
    const rules = {
      userId: 'required',
      userCarId: 'required',
      orderCategoryId: 'required',
      orderPrice: 'required'
    }

    const recovery = getState().recoveries.recoveryFormData
    const recoveryData = {
      userId: recovery.userId ? recovery.userId.value : undefined,
      userCarId: recovery.userCarId ? recovery.userCarId.value : undefined,
      carhootEmployeeId: recovery.carhootEmployeeId ? recovery.carhootEmployeeId.value : undefined,
      fieldAgentId: recovery.fieldAgentId ? recovery.fieldAgentId.value : undefined,
      vendorId: recovery.vendorId ? recovery.vendorId.value : undefined,
      vendorEmployeeId: recovery.vendorEmployeeId ? recovery.vendorEmployeeId.value : undefined,
      orderCategoryId: recovery.orderCategoryId ? recovery.orderCategoryId.value : undefined,
      orderPrice: recovery.orderPrice
    }

    const { isValid, errors } = allFieldsValidation(recoveryData, rules, {
      'required.userId': 'User is required.',
      'required.userCarId': 'User car is required.',
      'required.orderCategoryId': 'Order category is required.',
      'required.orderPrice': 'Order price is required.'
    })

    if (!isValid) {
      return dispatch({ type: SET_RECOVERIES_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_RECOVERIES_SUBMITTING, payload: true })
    dispatch({ type: SET_RECOVERIES_LOADING, payload: true })

    try {
      const response = await axios.post('/api/recoveries', recoveryData)
      dispatch({
        type: ADD_RECOVERY,
        recoveryData
      })
      dispatch({
        type: RESET_RECOVERY_FORM,
        payload: {}
      })
      dispatch(getRecoveryList(getState().recoveries.params))
      dispatch(getAllRecoveries())

      const successfulOptions = {
        title: `The recovery has been added successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to create the recovery. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_RECOVERIES_SUBMITTING, payload: false })
      dispatch({ type: SET_RECOVERIES_LOADING, payload: false })
    }
  }
}

// ** Delete recovery
export const updateRecovery = (_id) => {
  return async (dispatch, getState) => {
    const rules = {
      userId: 'required',
      userCarId: 'required',
      orderCategoryId: 'required',
      orderPrice: 'required'
    }

    const recovery = { ...getState().recoveries.selectedRecovery }
    const { isValid, errors } = allFieldsValidation(recovery, rules, {
      'required.userId': 'User is required.',
      'required.userCarId': 'User car is required.',
      'required.orderCategoryId': 'Order category is required.',
      'required.orderPrice': 'Order price is required.'
    })


    if (!isValid) {
      return dispatch({ type: SET_RECOVERIES_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_RECOVERIES_SUBMITTING, payload: true })
    dispatch({ type: SET_RECOVERIES_LOADING, payload: true })

    try {
      delete recovery._id
      delete recovery.categoryInfo
      delete recovery.employeeInfo
      delete recovery.fieldAgentInfo
      delete recovery.userCarInfo
      delete recovery.userInfo
      delete recovery.vendorEmployeeInfo
      delete recovery.vendorInfo
      delete recovery.createdAt
      delete recovery.updatedAt
      delete recovery.__v
      const response = await axios.put(`/api/recoveries/${_id}`, recovery)
      dispatch({
        type: RESET_RECOVERY_FORM,
        payload: {}
      })

      const successfulOptions = {
        title: `Recovery information was updated successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to update the recovery. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_RECOVERIES_SUBMITTING, payload: false })
      dispatch({ type: SET_RECOVERIES_LOADING, payload: false })
    }
  }
}

// ** Delete recovery
export const deactivateRecovery = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.post(`/api/recoveries/${id}/deactivate`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DEACTIVATE_RECOVERY
        })
      }
    } catch (error) {
      const title = `We were not able to delete the recovery. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_RECOVERIES_SUBMITTING, payload: false })
      dispatch({ type: SET_RECOVERIES_LOADING, payload: false })
    }
  }
}

// ** Delete recovery
export const deleteRecovery = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.delete(`/api/recoveries/${id}`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DELETE_RECOVERY
        })
      }
    } catch (error) {
      const title = `We were not able to delete the recovery. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_RECOVERIES_SUBMITTING, payload: false })
      dispatch({ type: SET_RECOVERIES_LOADING, payload: false })
    }
  }
}
