/*
 *
 * Employees actions
 *
 */

import axios from 'axios'
import { success } from "react-notification-system-redux"

import {
  RESET_EMPLOYEE_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET_EMPLOYEE,
  ADD_EMPLOYEE,
  DELETE_EMPLOYEE,
  SET_EMPLOYEES_LOADING,
  SET_EMPLOYEES_SUBMITTING,
  SET_EMPLOYEES_FORM_ERRORS,
  EMPLOYEE_FORM_CHANGE,
  EMPLOYEE_EDIT_CHANGE,
  DEACTIVATE_EMPLOYEE, FETCH_AGENTS
} from './constants'
import handleError from '@utils/error'
import { allFieldsValidation } from '@utils/validation'

export const employeeFormChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: EMPLOYEE_FORM_CHANGE,
    payload: formData
  }
}

export const employeeEditChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: EMPLOYEE_EDIT_CHANGE,
    payload: formData
  }
}

// ** Get all Data
export const getAllEmployees = (params = {}) => {
  return async dispatch => {
    await axios.get('/api/employees', { params }).then(response => {
      dispatch({
        type: GET_ALL_DATA,
        data: response.data.employees
      })
    })
  }
}

// ** Get all Data
export const getAllAgents = (params = {}) => {
  return async dispatch => {
    params.role = 'agent'
    await axios.get('/api/employees', { params }).then(response => {
      dispatch({
        type: FETCH_AGENTS,
        data: response.data.employees
      })
    })
  }
}

// ** Get data on page or row change
export const getEmployeeList = params => {
  return async dispatch => {
    await axios.get('/api/employees', { params }).then(response => {
      dispatch({
        type: GET_DATA,
        data: response.data.employees,
        totalPages: response.data.total,
        params
      })
    })
  }
}

// ** Get Employee
export const getEmployee = id => {
  return async dispatch => {
    await axios
      .get(`/api/employees/${id}`)
      .then(response => {
        dispatch({
          type: GET_EMPLOYEE,
          selectedEmployee: response.data.employee
        })
      })
      .catch(err => console.log(err))
  }
}

// ** Add employee
export const addEmployee = () => {
  return async (dispatch, getState) => {
    const rules = {
      firstName: 'required',
      lastName: 'required',
      userName: 'required',
      email: 'required|email',
      countryCode: 'required:number',
      phoneNumber: 'required:number',
      role: 'required'
    }

    const employee = getState().employees.employeeFormData
    const { isValid, errors } = allFieldsValidation(employee, rules, {
      'required.firstName': 'First Name is required.',
      'required.userName': 'userName is required.',
      'required.email': 'Email is required.',
      'email.email': 'Email format is invalid.',
      'required.countryCode': 'Country code is required.',
      'number.countryCode': 'Country code must be a number.',
      'required.phoneNumber': 'Phone number is required.',
      'number.phoneNumber': 'Phone number must be a number.',
      'required.role': 'Role is required.'
    })

    if (!isValid) {
      return dispatch({ type: SET_EMPLOYEES_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_EMPLOYEES_SUBMITTING, payload: true })
    dispatch({ type: SET_EMPLOYEES_LOADING, payload: true })

    try {
      const response = await axios.post('/api/employees', employee)
      dispatch({
        type: ADD_EMPLOYEE,
        employee
      })
      dispatch({
        type: RESET_EMPLOYEE_FORM,
        payload: {}
      })
      dispatch(getEmployeeList(getState().employees.params))
      dispatch(getAllEmployees())

      const successfulOptions = {
        title: `The employee has been added successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to create the employee. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_EMPLOYEES_SUBMITTING, payload: false })
      dispatch({ type: SET_EMPLOYEES_LOADING, payload: false })
    }
  }
}

// ** Delete employee
export const updateEmployee = (_id) => {
  return async (dispatch, getState) => {
    const rules = {
      firstName: 'required',
      lastName: 'required',
      userName: 'required',
      email: 'required|email',
      countryCode: 'required:number',
      phoneNumber: 'required:number',
      role: 'required'
    }

    const employee = { ...getState().employees.selectedEmployee }
    const { isValid, errors } = allFieldsValidation(employee, rules, {
      'required.firstName': 'First Name is required.',
      'required.userName': 'userName is required.',
      'required.email': 'Email is required.',
      'email.email': 'Email format is invalid.',
      'required.countryCode': 'Country code is required.',
      'number.countryCode': 'Country code must be a number.',
      'required.phoneNumber': 'Phone number is required.',
      'number.phoneNumber': 'Phone number must be a number.',
      'required.role': 'Role is required.'
    })


    if (!isValid) {
      return dispatch({ type: SET_EMPLOYEES_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_EMPLOYEES_SUBMITTING, payload: true })
    dispatch({ type: SET_EMPLOYEES_LOADING, payload: true })

    try {
      delete employee._id
      delete employee.createdAt
      delete employee.updatedAt
      if (employee.membershipType !== undefined) delete employee.membershipType
      delete employee.__v
      await axios.put(`/api/employees/${_id}`, employee)
      dispatch({
        type: RESET_EMPLOYEE_FORM,
        payload: {}
      })

      const successfulOptions = {
        title: `Employee information was updated successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to update the employee. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_EMPLOYEES_SUBMITTING, payload: false })
      dispatch({ type: SET_EMPLOYEES_LOADING, payload: false })
    }
  }
}

// ** Delete employee
export const deactivateEmployee = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.post(`/api/employees/${id}/deactivate`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DEACTIVATE_EMPLOYEE
        })
      }
    } catch (error) {
      const title = `We were not able to delete the employee. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_EMPLOYEES_SUBMITTING, payload: false })
      dispatch({ type: SET_EMPLOYEES_LOADING, payload: false })
    }
  }
}

// ** Delete employee
export const deleteEmployee = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.delete(`/api/employees/${id}`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DELETE_EMPLOYEE
        })
      }
    } catch (error) {
      const title = `We were not able to delete the employee. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_EMPLOYEES_SUBMITTING, payload: false })
      dispatch({ type: SET_EMPLOYEES_LOADING, payload: false })
    }
  }
}
