import axios from 'axios'
import { success } from "react-notification-system-redux"

import {
  ADD_VENDOR,
  ADD_VENDOR_EMPLOYEE,
  DEACTIVATE_VENDOR,
  DEACTIVATE_VENDOR_EMPLOYEE,
  DELETE_VENDOR,
  DELETE_VENDOR_EMPLOYEE,
  GET_ALL_DATA,
  GET_DATA,
  GET_EMPLOYEE_ALL_DATA,
  GET_EMPLOYEE_DATA,
  GET_VENDOR,
  GET_VENDOR_EMPLOYEE,
  RESET_VENDOR_EMPLOYEE_FORM,
  RESET_VENDOR_FORM,
  SET_VENDOR_EMPLOYEES_FORM_ERRORS,
  SET_VENDORS_FORM_ERRORS,
  SET_VENDORS_LOADING,
  SET_VENDORS_SUBMITTING,
  VENDOR_EDIT_CHANGE,
  VENDOR_EMPLOYEE_EDIT_CHANGE,
  VENDOR_EMPLOYEE_FORM_CHANGE,
  VENDOR_FORM_CHANGE
} from './constants'
import handleError from '@utils/error'
import { allFieldsValidation } from '@utils/validation'

export const vendorFormChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: VENDOR_FORM_CHANGE,
    payload: formData
  }
}

export const vendorEditChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: VENDOR_EDIT_CHANGE,
    payload: formData
  }
}

// ** Get all Data
export const getAllVendors = (params = {}) => {
  return async dispatch => {
    await axios.get('/api/vendors', { params }).then(response => {
      dispatch({
        type: GET_ALL_DATA,
        data: response.data.vendors
      })
    })
  }
}

// ** Get data on page or row change
export const getVendorList = params => {
  return async dispatch => {
    await axios.get('/api/vendors', { params }).then(response => {
      dispatch({
        type: GET_DATA,
        data: response.data.vendors,
        totalPages: response.data.total,
        params
      })
    })
  }
}

// ** Get Vendor
export const getVendor = id => {
  return async dispatch => {
    await axios
      .get(`/api/vendors/${id}`)
      .then(response => {
        dispatch({
          type: GET_VENDOR,
          selectedVendor: response.data.vendor
        })
      })
      .catch(err => console.log(err))
  }
}

// ** Add vendor
export const addVendor = () => {
  return async (dispatch, getState) => {
    const rules = {
      vendorName: 'required',
      ownerName: 'required',
      email: 'required|email',
      countryCode: 'required:number',
      phoneNumber: 'required:number',
      entityType: 'required'
    }

    const vendor = getState().vendors.vendorFormData
    const { isValid, errors } = allFieldsValidation(vendor, rules, {
      'required.vendorName': 'Vendor name is required.',
      'required.ownerName': 'Owner name is required.',
      'required.email': 'Email is required.',
      'email.email': 'Email format is invalid.',
      'required.countryCode': 'Country code is required.',
      'number.countryCode': 'Country code must be a number.',
      'required.phoneNumber': 'Phone number is required.',
      'number.phoneNumber': 'Phone number must be a number.',
      'required.entityType': 'Entity type is required.'
    })

    if (!isValid) {
      return dispatch({ type: SET_VENDORS_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_VENDORS_SUBMITTING, payload: true })
    dispatch({ type: SET_VENDORS_LOADING, payload: true })
    try {
      const response = await axios.post('/api/vendors', vendor)
      dispatch({
        type: ADD_VENDOR,
        vendor
      })
      dispatch({
        type: RESET_VENDOR_FORM,
        payload: {}
      })
      dispatch(getVendorList(getState().vendors.params))
      dispatch(getAllVendors())

      const successfulOptions = {
        title: `The vendor has been added successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to create the vendor. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_VENDORS_SUBMITTING, payload: false })
      dispatch({ type: SET_VENDORS_LOADING, payload: false })
    }
  }
}

// ** Delete vendor
export const updateVendor = (_id) => {
  return async (dispatch, getState) => {
    const rules = {
      vendorName: 'required',
      ownerName: 'required',
      email: 'required|email',
      countryCode: 'required:number',
      phoneNumber: 'required:number',
      websiteURL: 'required',
      entityType: 'required'
    }

    const vendor = { ...getState().vendors.selectedVendor }
    const { isValid, errors } = allFieldsValidation(vendor, rules, {
      'required.vendorName': 'Vendor name is required.',
      'required.ownerName': 'Owner name is required.',
      'required.email': 'Email is required.',
      'email.email': 'Email format is invalid.',
      'required.countryCode': 'Country code is required.',
      'number.countryCode': 'Country code must be a number.',
      'required.phoneNumber': 'Phone number is required.',
      'number.phoneNumber': 'Phone number must be a number.',
      'required.entityType': 'Entity type is required.',
      'required.websiteURL': 'Website is required.'
    })


    if (!isValid) {
      return dispatch({ type: SET_VENDORS_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_VENDORS_SUBMITTING, payload: true })
    dispatch({ type: SET_VENDORS_LOADING, payload: true })

    try {
      delete vendor._id
      delete vendor.createdAt
      delete vendor.updatedAt
      if (vendor.membershipType !== undefined) delete vendor.membershipType
      if (vendor.role !== undefined) delete vendor.role
      delete vendor.__v
      await axios.put(`/api/vendors/${_id}`, vendor)
      dispatch({
        type: RESET_VENDOR_FORM,
        payload: {}
      })
      const successfulOptions = {
        title: `Vendor information was updated successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to update the vendor. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_VENDORS_SUBMITTING, payload: false })
      dispatch({ type: SET_VENDORS_LOADING, payload: false })
    }
  }
}

// ** Delete vendor
export const deactivateVendor = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.post(`/api/vendors/${id}/deactivate`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DEACTIVATE_VENDOR
        })
      }
    } catch (error) {
      const title = `We were not able to delete the vendor. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_VENDORS_SUBMITTING, payload: false })
      dispatch({ type: SET_VENDORS_LOADING, payload: false })
    }
  }
}

