// ** Add employee
export const addVendorEmployee = () => {
  return async (dispatch, getState) => {
    const selectedVendor = getState().vendors.selectedVendor
    const rules = {
      firstName: 'required',
      lastName: 'required',
      userName: 'required',
      email: 'required|email',
      password: 'required|min:6',
      countryCode: 'required:number',
      phoneNumber: 'required:number',
      role: 'required'
    }

    const employee = getState().vendors.vendorEmployeeFormData
    const { isValid, errors } = allFieldsValidation(employee, rules, {
      'required.firstName': 'First Name is required.',
      'required.userName': 'userName is required.',
      'required.email': 'Email is required.',
      'email.email': 'Email format is invalid.',
      'required.password': 'Password is required.',
      'min.password': 'Password must be at least 6 characters.',
      'required.countryCode': 'Country code is required.',
      'number.countryCode': 'Country code must be a number.',
      'required.phoneNumber': 'Phone number is required.',
      'number.phoneNumber': 'Phone number must be a number.',
      'required.role': 'Role is required.'
    })

    if (!isValid) {
      return dispatch({ type: SET_VENDOR_EMPLOYEES_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_VENDORS_SUBMITTING, payload: true })
    dispatch({ type: SET_VENDORS_LOADING, payload: true })

    try {
      await axios.post(`/api/vendors/${selectedVendor._id}/employees`, employee)
      dispatch({
        type: ADD_VENDOR_EMPLOYEE,
        employee
      })
      dispatch({
        type: RESET_VENDOR_EMPLOYEE_FORM,
        payload: {}
      })
      dispatch(getVendorEmployeeList(getState().vendors.employeeParams))
      dispatch(getAllVendorEmployees(selectedVendor._id))

      const successfulOptions = {
        title: `The employee has been added successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to create the employee. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_VENDORS_SUBMITTING, payload: false })
      dispatch({ type: SET_VENDORS_LOADING, payload: false })
    }
  }
}

