import axios from 'axios'
import { success } from "react-notification-system-redux"

import {
  RESET_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET,
  ADD,
  DELETE,
  SET_LOADING,
  SET_SUBMITTING,
  SET_FORM_ERRORS,
  FORM_CHANGE,
  EDIT_CHANGE,
  DEACTIVATE,
  FETCH
} from './constants'
import handleError from '@utils/error'
import { allFieldsValidation } from '@utils/validation'

export const roleFormChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: FORM_CHANGE,
    payload: formData
  }
}

export const roleEditChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: EDIT_CHANGE,
    payload: formData
  }
}

// ** Get Role Configuration
export const getRolesConfiguration = () => {
  return async dispatch => {
    await axios.get('/api/roles').then(response => {
      const rolesConfig = {}
      for (const role of response.data.roles) {
        rolesConfig[role.type] = role
      }

      dispatch({
        type: FETCH,
        data: rolesConfig
      })
    })
  }
}

// ** Get all Data
export const getAllRoles = () => {
  return async dispatch => {
    await axios.get('/api/roles').then(response => {
      dispatch({
        type: GET_ALL_DATA,
        data: response.data.roles
      })
    })
  }
}

// ** Get data on page or row change
export const getRoleList = params => {
  return async dispatch => {
    await axios.get('/api/roles', { params }).then(response => {
      dispatch({
        type: GET_DATA,
        data: response.data.roles,
        totalPages: response.data.total,
        params
      })
    })
  }
}

