// ** Add role
export const addRole = () => {
  return async (dispatch, getState) => {
    const rules = {
      serviceName: 'required',
      serviceCategory: 'required',
      serviceSubCategory: 'required',
      serviceIconUrl: 'required',
      basePrice: 'required'
    }

    const role = getState().roles.roleFormData
    const { isValid, errors } = allFieldsValidation(role, rules, {
      'required.serviceName': 'Service name is required.',
      'required.serviceCategory': 'Category is required.',
      'required.serviceSubCategory': 'Subcategory is required.',
      'required.serviceIconUrl': 'Icon Url is required.',
      'required.basePrice': 'Base price is required.'
    })

    if (!isValid) {
      return dispatch({ type: SET_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_SUBMITTING, payload: true })
    dispatch({ type: SET_LOADING, payload: true })

    try {
      const response = await axios.post('/api/roles', role)
      dispatch({
        type: ADD,
        role
      })
      dispatch({
        type: RESET_FORM,
        payload: {}
      })
      dispatch(getRoleList(getState().roles.params))
      dispatch(getAllRoles())

      const successfulOptions = {
        title: `The role has been added successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to create the role. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete role
export const updateRole = (_id) => {
  return async (dispatch, getState) => {
    const rules = {
      serviceName: 'required',
      serviceCategory: 'required',
      serviceSubCategory: 'required',
      serviceIconUrl: 'required',
      basePrice: 'required'
    }

    const role = { ...getState().roles.selectedRole }
    const { isValid, errors } = allFieldsValidation(role, rules, {
      'required.serviceName': 'Service name is required.',
      'required.serviceCategory': 'Category is required.',
      'required.serviceSubCategory': 'Subcategory is required.',
      'required.serviceIconUrl': 'Icon Url is required.',
      'required.basePrice': 'Base price is required.'
    })


    if (!isValid) {
      return dispatch({ type: SET_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_SUBMITTING, payload: true })
    dispatch({ type: SET_LOADING, payload: true })

    try {
      delete role._id
      delete role.createdAt
      delete role.updatedAt
      delete role.userInfo
      delete role.__v
      await axios.put(`/api/roles/${_id}`, role)
      dispatch({
        type: RESET_FORM,
        payload: {}
      })

      const successfulOptions = {
        title: `Role information was updated successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to update the role. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete role
export const deactivateRole = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.post(`/api/roles/${id}/deactivate`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DEACTIVATE
        })
      }
    } catch (error) {
      const title = `We were not able to delete the role. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}

// ** Delete role
export const deleteRole = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.delete(`/api/roles/${id}`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DELETE
        })
      }
    } catch (error) {
      const title = `We were not able to delete the role. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_SUBMITTING, payload: false })
      dispatch({ type: SET_LOADING, payload: false })
    }
  }
}
