/*
 *
 * Login actions
 *
 */

import { success } from 'react-notification-system-redux'
import axios from 'axios'
import { push } from 'connected-react-router'

import {
  LOGIN_CHANGE,
  LOGIN_RESET,
  SET_LOGIN_LOADING,
  SET_LOGIN_FORM_ERRORS,
  SET_LOGIN_SUBMITTING
} from './constants'
import { setAuth, clearAuth } from '../Authentication/actions'
import setToken from '@utils/token'
import handleError from '@utils/error'
import { clearAccount } from '@store/Account/actions'
import { allFieldsValidation } from '@utils/validation'
// ** UseJWT import to get config
import useJwt from '@src/auth/jwt/useJwt'

const config = useJwt.jwtConfig

export const loginChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: LOGIN_CHANGE,
    payload: formData
  }
}

export const login = () => {
  return async (dispatch, getState) => {
    const rules = {
      email: 'required|email',
      password: 'required|min:6'
    }

    const user = getState().login.loginFormData

    const { isValid, errors } = allFieldsValidation(user, rules, {
      'required.email': 'Email is required.',
      'email.email': 'Email format is invalid.',
      'required.password': 'Password is required.',
      'min.password': 'Password must be at least 6 characters.'
    })

    if (!isValid) {
      return dispatch({ type: SET_LOGIN_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_LOGIN_SUBMITTING, payload: true })
    dispatch({ type: SET_LOGIN_LOADING, payload: true })

    try {
      const response = await axios.post('/api/auth/login', user)

      const firstName = response.data.user.firstName

      const successfulOptions = {
        title: `Hey${firstName ? ` ${firstName}` : ''}, Welcome Back!`,
        position: 'tr',
        autoDismiss: 1
      }

      localStorage.setItem('token', response.data.token)
      // ** Add to user, accessToken & refreshToken to localStorage
      localStorage.setItem('userData', JSON.stringify(response.data.user))
      localStorage.setItem(config.storageTokenKeyName, response.data.token)
      localStorage.setItem(config.storageRefreshTokenKeyName, response.data.token)

      setToken(response.data.token)

      dispatch(setAuth({
        data: response.data.user,
        config,
        [config.storageTokenKeyName]: response.data.token,
        [config.storageRefreshTokenKeyName]: response.data.token
      }))
      dispatch(success(successfulOptions))

      dispatch({ type: LOGIN_RESET })
    } catch (error) {
      const title = `Please try to login again!`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_LOGIN_SUBMITTING, payload: false })
      dispatch({ type: SET_LOGIN_LOADING, payload: false })
    }
  }
}

export const signOut = () => {
  return (dispatch, getState) => {
    const successfulOptions = {
      title: `You have signed out!`,
      position: 'tr',
      autoDismiss: 1
    }

    dispatch(clearAuth({
      [config.storageTokenKeyName]: null, [config.storageRefreshTokenKeyName]: null
    }))
    dispatch(clearAccount())
    dispatch(push('/login'))

    localStorage.removeItem('token')
    // ** Remove user, accessToken & refreshToken from localStorage
    localStorage.removeItem('userData')
    localStorage.removeItem(config.storageTokenKeyName)
    localStorage.removeItem(config.storageRefreshTokenKeyName)

    dispatch(success(successfulOptions))
    // dispatch(clearCart())
  }
}
