/**
 *
 * actions.js
 * actions configuration
 */

import { bindActionCreators } from 'redux'

import * as application from '@store/Application/actions'
import * as authentication from '@store/Authentication/actions'
import * as signup from '@store/SignUp/actions'
import * as login from '@store/Login/actions'
import * as forgotPassword from '@store/ForgotPassword/actions'
import * as resetPassword from '@store/ResetPassword/actions'
import * as account from '@store/Account/actions'
import * as users from '@store/Users/actions'
import * as employees from '@store/Employees/actions'
import * as vendors from '@store/Vendors/actions'
import * as recoveries from '@store/RecoveryRequest/actions'
import * as userCars from '@store/UserCars/actions'

export default function mapDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      ...application,
      ...authentication,
      ...signup,
      ...login,
      ...forgotPassword,
      ...resetPassword,
      ...account,
      ...users,
      ...employees,
      ...vendors,
      ...recoveries,
      ...userCars
    },
    dispatch
  )
}
