import { lazy } from 'react'
import { Redirect } from 'react-router-dom'

const Vendors = [
  {
    path: '/vendors/list',
    component: lazy(() => import('../../views/UsersVendor/List'))
  },
  {
    path: '/vendors/edit/:id',
    component: lazy(() => import('../../views/UsersVendor/Edit'))
  },
  {
    path: '/vendors/view/:id',
    component: lazy(() => import('../../views/UsersVendor/View'))
  },
  {
    path: '/vendors/:vendorId/employees/edit/:id',
    component: lazy(() => import('../../views/VendorEmployee/Edit'))
  },
  {
    path: '/vendors/:vendorId/employees/view/:id',
    component: lazy(() => import('../../views/VendorEmployee/View'))
  }
]

export default Vendors
