/*
 *
 * Users actions
 *
 */

import axios from 'axios'
import { success } from "react-notification-system-redux"

import {
  RESET_USER_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET_USER,
  ADD_USER,
  DELETE_USER,
  SET_USERS_LOADING,
  SET_USERS_SUBMITTING,
  SET_USERS_FORM_ERRORS,
  USER_FORM_CHANGE,
  USER_EDIT_CHANGE,
  DEACTIVATE_USER
} from './constants'
import handleError from '@utils/error'
import { allFieldsValidation } from '@utils/validation'

export const userFormChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: USER_FORM_CHANGE,
    payload: formData
  }
}

export const userEditChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: USER_EDIT_CHANGE,
    payload: formData
  }
}

// ** Get all Data
export const getAllUsers = (params = {}) => {
  return async dispatch => {
    await axios.get('/api/users', { params }).then(response => {
      dispatch({
        type: GET_ALL_DATA,
        data: response.data.users
      })
    })
  }
}

// ** Get data on page or row change
export const getUserList = params => {
  return async dispatch => {
    await axios.get('/api/users', { params }).then(response => {
      dispatch({
        type: GET_DATA,
        data: response.data.users,
        totalPages: response.data.total,
        params
      })
    })
  }
}

// ** Get User
export const getUser = id => {
  return async dispatch => {
    await axios
      .get(`/api/users/${id}`)
      .then(response => {
        dispatch({
          type: GET_USER,
          selectedUser: response.data.user
        })
      })
      .catch(err => console.log(err))
  }
}

// ** Add user
export const addUser = () => {
  return async (dispatch, getState) => {
    const rules = {
      firstName: 'required',
      lastName: 'required',
      userName: 'required',
      email: 'required|email',
      password: 'required|min:6',
      countryCode: 'required:number',
      phoneNumber: 'required:number'
    }

    const user = getState().users.userFormData
    const { isValid, errors } = allFieldsValidation(user, rules, {
      'required.firstName': 'First Name is required.',
      'required.userName': 'Username is required.',
      'required.email': 'Email is required.',
      'email.email': 'Email format is invalid.',
      'required.password': 'Password is required.',
      'min.password': 'Password must be at least 6 characters.',
      'required.countryCode': 'Country code is required.',
      'number.countryCode': 'Country code must be a number.',
      'required.phoneNumber': 'Phone number is required.',
      'number.phoneNumber': 'Phone number must be a number.'
    })

    if (!isValid) {
      return dispatch({ type: SET_USERS_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_USERS_SUBMITTING, payload: true })
    dispatch({ type: SET_USERS_LOADING, payload: true })

    try {
      const response = await axios.post('/api/users', user)
      dispatch({
        type: ADD_USER,
        user
      })
      dispatch({
        type: RESET_USER_FORM,
        payload: {}
      })
      dispatch(getUserList(getState().users.params))
      dispatch(getAllUsers())

      const successfulOptions = {
        title: `The user has been added successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to create the user. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_USERS_SUBMITTING, payload: false })
      dispatch({ type: SET_USERS_LOADING, payload: false })
    }
  }
}

// ** Delete user
export const updateUser = (_id) => {
  return async (dispatch, getState) => {
    const rules = {
      firstName: 'required',
      lastName: 'required',
      userName: 'required',
      email: 'required|email',
      password: 'min:6',
      countryCode: 'required:number',
      phoneNumber: 'required:number'
    }

    const user = { ...getState().users.selectedUser }
    const { isValid, errors } = allFieldsValidation(user, rules, {
      'required.firstName': 'First Name is required.',
      'required.userName': 'Username is required.',
      'required.email': 'Email is required.',
      'email.email': 'Email format is invalid.',
      'min.password': 'Password must be at least 6 characters.',
      'required.countryCode': 'Country code is required.',
      'number.countryCode': 'Country code must be a number.',
      'required.phoneNumber': 'Phone number is required.',
      'number.phoneNumber': 'Phone number must be a number.'
    })


    if (!isValid) {
      return dispatch({ type: SET_USERS_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_USERS_SUBMITTING, payload: true })
    dispatch({ type: SET_USERS_LOADING, payload: true })

    try {
      delete user._id
      delete user.createdAt
      delete user.updatedAt
      if (user.role !== undefined) delete user.role
      delete user.__v
      await axios.put(`/api/users/${_id}`, user)
      dispatch({
        type: RESET_USER_FORM,
        payload: {}
      })

      const successfulOptions = {
        title: `User information was updated successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to update the user. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_USERS_SUBMITTING, payload: false })
      dispatch({ type: SET_USERS_LOADING, payload: false })
    }
  }
}

// ** Delete user
export const deactivateUser = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.post(`/api/users/${id}/deactivate`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DEACTIVATE_USER
        })
      }
    } catch (error) {
      const title = `We were not able to delete the user. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_USERS_SUBMITTING, payload: false })
      dispatch({ type: SET_USERS_LOADING, payload: false })
    }
  }
}

// ** Delete user
export const deleteUser = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.delete(`/api/users/${id}`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DELETE_USER
        })
      }
    } catch (error) {
      const title = `We were not able to delete the user. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_USERS_SUBMITTING, payload: false })
      dispatch({ type: SET_USERS_LOADING, payload: false })
    }
  }
}
