/*
 *
 * UserCars actions
 *
 */

import axios from 'axios'
import { success } from "react-notification-system-redux"

import {
  RESET_USERCAR_FORM,
  GET_ALL_DATA,
  GET_DATA,
  GET_USERCAR,
  ADD_USERCAR,
  DELETE_USERCAR,
  SET_USERCARS_LOADING,
  SET_USERCARS_SUBMITTING,
  SET_USERCARS_FORM_ERRORS,
  USERCAR_FORM_CHANGE,
  USERCAR_EDIT_CHANGE,
  DEACTIVATE_USERCAR
} from './constants'
import handleError from '@utils/error'
import { allFieldsValidation } from '@utils/validation'

export const userCarFormChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: USERCAR_FORM_CHANGE,
    payload: formData
  }
}

export const userCarEditChange = (name, value) => {
  const formData = {}
  formData[name] = value

  return {
    type: USERCAR_EDIT_CHANGE,
    payload: formData
  }
}

// ** Get all Data
export const getAllUserCars = (params) => {
  return async dispatch => {
    await axios.get('/api/userCars', { params }).then(response => {
      dispatch({
        type: GET_ALL_DATA,
        data: response.data.userCars
      })
    })
  }
}

// ** Get data on page or row change
export const getUserCarList = params => {
  return async dispatch => {
    await axios.get('/api/userCars', { params }).then(response => {
      dispatch({
        type: GET_DATA,
        data: response.data.userCars,
        totalPages: response.data.total,
        params
      })
    })
  }
}

// ** Get UserCars
export const getUserCar = id => {
  return async dispatch => {
    await axios
      .get(`/api/userCars/${id}`)
      .then(response => {
        dispatch({
          type: GET_USERCAR,
          selectedUserCar: response.data.userCar
        })
      })
      .catch(err => console.log(err))
  }
}

// ** Add userCar
export const addUserCar = () => {
  return async (dispatch, getState) => {
    const rules = {
      carMake: 'required',
      carModel: 'required',
      engineCapacity: 'required',
      registrationNumber: 'required',
      yearOfManufacture: 'required',
      carStatus: 'required'
    }

    const userCar = getState().userCars.userCarFormData
    const { isValid, errors } = allFieldsValidation(userCar, rules, {
      'required.carMake': 'Make is required.',
      'required.carModel': 'Mode is required.',
      'required.engineCapacity': 'Engine is required.',
      'required.registrationNumber': 'Registration number is required.',
      'required.yearOfManufacture': 'Year of manufacture is required.',
      'required.carStatus': 'Car status is required.'
    })

    if (!isValid) {
      return dispatch({ type: SET_USERCARS_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_USERCARS_SUBMITTING, payload: true })
    dispatch({ type: SET_USERCARS_LOADING, payload: true })

    const selectedUser = getState().users.selectedUser
    userCar.userId = selectedUser._id

    try {
      const response = await axios.post('/api/userCars', userCar)
      dispatch({
        type: ADD_USERCAR,
        userCar
      })
      dispatch({
        type: RESET_USERCAR_FORM,
        payload: {}
      })
      dispatch(getUserCarList(getState().userCars.params))
      dispatch(getAllUserCars())

      const successfulOptions = {
        title: `The userCar has been added successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to create the userCar. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_USERCARS_SUBMITTING, payload: false })
      dispatch({ type: SET_USERCARS_LOADING, payload: false })
    }
  }
}

// ** Delete userCar
export const updateUserCar = (_id) => {
  return async (dispatch, getState) => {
    const rules = {
      carMake: 'required',
      carModel: 'required',
      engineCapacity: 'required',
      registrationNumber: 'required',
      yearOfManufacture: 'required',
      carStatus: 'required'
    }

    const userCar = { ...getState().userCars.selectedUserCar }
    const { isValid, errors } = allFieldsValidation(userCar, rules, {
      'required.carMake': 'Make is required.',
      'required.carModel': 'Mode is required.',
      'required.engineCapacity': 'Engine is required.',
      'required.registrationNumber': 'Registration number is required.',
      'required.yearOfManufacture': 'Year of manufacture is required.',
      'required.carStatus': 'Car status is required.'
    })


    if (!isValid) {
      return dispatch({ type: SET_USERCARS_FORM_ERRORS, payload: errors })
    }

    dispatch({ type: SET_USERCARS_SUBMITTING, payload: true })
    dispatch({ type: SET_USERCARS_LOADING, payload: true })

    try {
      delete userCar._id
      delete userCar.createdAt
      delete userCar.updatedAt
      delete userCar.userInfo
      delete userCar.__v
      await axios.put(`/api/userCars/${_id}`, userCar)
      dispatch({
        type: RESET_USERCAR_FORM,
        payload: {}
      })

      const successfulOptions = {
        title: `UserCar information was updated successfully!`,
        position: 'tr',
        autoDismiss: 1
      }

      dispatch(success(successfulOptions))
    } catch (error) {
      const title = `We were not able to update the userCar. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_USERCARS_SUBMITTING, payload: false })
      dispatch({ type: SET_USERCARS_LOADING, payload: false })
    }
  }
}

// ** Delete userCar
export const deactivateUserCar = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.post(`/api/userCars/${id}/deactivate`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DEACTIVATE_USERCAR
        })
      }
    } catch (error) {
      const title = `We were not able to delete the userCar. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_USERCARS_SUBMITTING, payload: false })
      dispatch({ type: SET_USERCARS_LOADING, payload: false })
    }
  }
}

// ** Delete userCar
export const deleteUserCar = id => {
  return async (dispatch, getState) => {
    try {
      const response = await axios.delete(`/api/userCars/${id}`)
      if (response.data.success === true) {
        const successfulOptions = {
          title: `${response.data.message}`,
          position: 'tr',
          autoDismiss: 1
        }
        dispatch(success(successfulOptions))
        dispatch({
          type: DELETE_USERCAR
        })
      }
    } catch (error) {
      const title = `We were not able to delete the userCar. Please try again.`
      handleError(error, dispatch, title)
    } finally {
      dispatch({ type: SET_USERCARS_SUBMITTING, payload: false })
      dispatch({ type: SET_USERCARS_LOADING, payload: false })
    }
  }
}
